
import { defineComponent } from 'vue'
import { useToast } from "vue-toastification";
import { POSITION } from "vue-toastification";

export default defineComponent({
  name: 'EmailIndex',

  data () {
    return {
      rules: [(v: any) => v.length <= 1024 || 'Max 1024 characters'],

      email:{
        name: "",
        email: "",
        body: ""
      }
    }
  },
  methods:{
    scrollToTop() {
      window.scrollTo(0,0);
    },

    clear(){
      this.email.name = "";
      this.email.email = "";
      this.email.body = "";
    },

    submit_email(){

      this.clear();
      
      const toast = useToast();
      toast.success("Submitted your queries successfully.", {
        timeout: 2000,
        position: POSITION.BOTTOM_RIGHT
      });
    }
  },
  
  mounted(){
    this.scrollToTop();
  }
})
